.blue{
	color: #4A90E2;
}

.red{
	color: #D0021B;
}

.green{
	color: #7ED321;
}

.index-wrapper{
	padding: 30px 42px 42px;
	margin: 0 auto;
}

@media (min-width: 1024px){

	.index-wrapper{
		width: 1024px;
	}
}

.index-wrapper section.index-title{
	margin-bottom: 30px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.index-wrapper section.index-title h2{
	font-size: 24px;
	color: black;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}

.index-wrapper section.index-title .set-staus{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.index-wrapper section.index-title .set-staus span{
	color: #9B9B9B;
	font-size: 16px;
}

.index-wrapper section.index-title .set-staus i{
	margin: 0px 8px;
}

.index-wrapper section.index-title .set-staus .day{
	margin-right: 20px;
}

.index-wrapper section.index-title .set-staus .time{
	cursor: pointer;
}

.index-wrapper .panel{
	border-radius: 3px;
	background-color: white;
	-webkit-box-shadow: #EBEBEB 0 0 10px 5px;
	        box-shadow: #EBEBEB 0 0 10px 5px;
}

.index-wrapper section.index-top-price{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 18px;
}

.index-wrapper section.index-top-price .panel{
	height: 138px;
	width: calc(33.33333% - 13.33333px);
	min-width: 300px;
	margin-right: 20px;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}

.index-wrapper section.index-top-price .panel:last-child{
	margin-right: 0;
}

.index-wrapper section.index-top-price .title{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	margin-bottom: 16px;
}

.index-wrapper section.index-top-price .title h5{
	font-size: 16px;
	line-height: 19px;
	font-weight: bolder;
	color: black;
	text-align: center;
}

.index-wrapper section.index-top-price .title i{
	margin-right: 8px;
	font-size: 16px;
}

.index-wrapper section.index-top-price .num{
	font-size: 36px;
	line-height: 44px;
	text-align: center;
	font-weight: bolder;
}

.index-wrapper section.index-mid-chart{
	width: 100%;
	min-width: 940px;
	height: 406px;
	padding: 30px 40px;
	margin-bottom: 18px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.index-wrapper section.index-mid-chart h2{
	margin-bottom: 16px;
}

.index-wrapper section.index-mid-chart #chart{
	overflow: hidden;
	-ms-flex-item-align: center;
	    align-self: center;
}

.index-wrapper section.index-mid-chart #chart rect:nth-child(2), 
			.index-wrapper section.index-mid-chart #chart rect:nth-child(4), 
			.index-wrapper section.index-mid-chart #chart rect:nth-child(6){
	display: none;
}

.index-wrapper section.index-bot-info{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.index-wrapper section.index-bot-info .panel{
	padding: 30px 40px;
	width: calc(50% - 10px);
	min-width: 460px;
}

.index-wrapper section.index-bot-info .panel h5{
	font-size: 24px;
	line-height: 29px;
	font-weight: bolder;
	margin-bottom: 30px;
}

.index-wrapper section.index-bot-info .panel .item{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding: 24px 0;
	border-bottom: 1px solid #EBEBEB;
}

.index-wrapper section.index-bot-info .panel .item i{
	font-size: 45px;
}

.index-wrapper section.index-bot-info .panel .item i, .index-wrapper section.index-bot-info .panel .item img{
	width: 45px;
	height: 45px;
	margin-right: 20px;
}

.index-wrapper section.index-bot-info .panel .item .sitename{
	font-size: 16px;
	line-height: 19px;
	color: #9B9B9B;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}

.index-wrapper section.index-bot-info .panel .item .view{
	font-size: 20px;
	line-height: 20px;
	font-weight: bolder;
	margin-right: 20px;
}

.index-wrapper section.index-bot-info .panel .item .num{
	font-size: 16px;
	line-height: 19px;
	font-weight: bold;
}

.index-wrapper section.index-bot-info .panel .item .num i{
	width: auto;
	height: auto;
	font-size: 16px;
	margin-right: 8px;
}

.index-wrapper section.index-bot-info .panel .item:nth-child(2){
	padding-top: 0;
}

.index-wrapper section.index-bot-info .panel .item:last-child{
	border: none;
	padding-bottom: 0;
}

.index-wrapper section.index-bot-info .panel .order{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding: 8px 0;
	border-bottom: 1px solid #EBEBEB;
}

.index-wrapper section.index-bot-info .panel .order .img{
	width: 100px;
	height: 100px;
	background-size: cover;
	margin-right: 21px;
}

.index-wrapper section.index-bot-info .panel .order .text{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}

.index-wrapper section.index-bot-info .panel .order .text h5{
	font-size: 20px;
	line-height: 25px;
	margin-bottom: 8px;
}

.index-wrapper section.index-bot-info .panel .order .text span{
	font-size: 16px;
	line-height: 18px;
	color: #9B9B9B;
	display: table;
}

.index-wrapper section.index-bot-info .panel .order .total{
	-ms-flex-item-align: end;
	    align-self: flex-end;
	margin-bottom: 15.5px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}

.index-wrapper section.index-bot-info .panel .order .total span{
	display: table;
}

.index-wrapper section.index-bot-info .panel .order .total .num{
	font-weight: bold;
}

.index-wrapper section.index-bot-info .panel .order:nth-child(2){
	padding-top: 0;
}

.index-wrapper section.index-bot-info .panel .order:last-child{
	padding-bottom: 0;
	border: none;
}

.index-wrapper section.index-bot-info .panel:first-child{
	margin-right: 20px;
}


