section.orders{
    width: 940px;
    margin: 30px auto 100px auto
}
section.orders .change-staus{
    margin-bottom: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
}
section.orders .change-staus .selector{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding-left: 16px
}
section.orders .change-staus .selector .check-wrapper{
    display: inline-block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    height: 15px;
    width: 15px;
    margin-right: 5px
}
section.orders .change-staus .selector .check-wrapper .checkmark{
    position: absolute;
    top: 0;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: white;
    border: 1px solid black;
    border-radius: 2px
}
section.orders .change-staus .selector .check-wrapper .checkmark:after{
    content: '';
    position: absolute;
    display: none;
    left: 4px;
    top: 1px;
    width: 3px;
    height: 6px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
section.orders .change-staus .selector .check-wrapper input:checked ~ .checkmark{
    background-color: black
}
section.orders .change-staus .selector .check-wrapper input:checked ~ .checkmark:after{
    display: block;
}
section.orders .change-staus .edit h5{
    display: inline-block;
    margin-right: 5px;
}
section.orders .order-title{
    background-color: white;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 10px 16px;
    height: 40px
}
section.orders .order-title h5{
    font-size: 16px;
    line-height: 19px;
    font-weight: bolder;
    padding: 0 16px;
}
section.orders .order-title h5:nth-child(1),
        section.orders .order-title h5:nth-child(2),
        section.orders .order-title h5:nth-child(6){
    width: 160px;
}
section.orders .order-title h5:nth-child(3){
    width: 70px;
}
section.orders .order-title h5:nth-child(4){
    width: 123px;
}
section.orders .order-title h5:nth-child(5){
    width: 111px;
}
section.orders .order-title h5:nth-child(7){
    width: 120px;
}
section.orders .page-num{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
    margin-top: 30px
}
section.orders .page-num i, section.orders .page-num span{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 40px;
    height: 40px;
    color: black;
    font-size: 16px;
    -webkit-transition: .2s;
    transition: .2s;
    background-color: white;
    border: 1px solid #ECEEEF;
    border-left: none;
    cursor: pointer
}
section.orders .page-num i:hover,section.orders .page-num span:hover{
    background-color: #9B9B9B;
    color: white;
    -webkit-transition: .2s;
    transition: .2s;
}
section.orders .page-num i{
    font-size: 12px;
}
section.orders .page-num i:nth-child(1){
    color: #9B9B9B;
    border-left: 1px solid #ECEEEF;
}
section.orders .page-num .selected{
    background-color: black;
    color: white;
}


