.order-content{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 16px
    
}
.order-content span{
    font-size: 14px;
    line-height: 21px;
    display: table;
    
}
.order-content .product-list, .order-content .total, .order-content .cart, .order-content .checkout, .order-content .address, .order-content .option{
    padding: 0 16px;
    
}
.order-content .name, .order-content .product-list, .order-content .address{
    width: 160px;
    
}
.order-content .total{
    width: 70px;
    
}
.order-content .cart{
    width: 123px;
    
}
.order-content .checkout{
    width: 111px;
    
}
.order-content .name{
    padding-right: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex
    
}
.order-content .name .checkbox-wrapper{
    position: relative;
    display: block;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    height: 15px;
    width: 15px;
    margin-right: 5px
    
}
.order-content .name .checkbox-wrapper .checkmark{
    position: absolute;
    top: 0;
    left: 0;
    height: 15px;
    width: 15px;
    background-color: white
    
}
.order-content .name .checkbox-wrapper .checkmark:after{
    content: '';
    position: absolute;
    display: none;
    left: 5px;
    top: 2px;
    width: 3px;
    height: 6px;
    border: solid white;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    
}
.order-content .name .checkbox-wrapper input:checked ~ .checkmark{
    /* checkbox checked color */
    background-color: #9B9B9B
    
}
.order-content .name .checkbox-wrapper input:checked ~ .checkmark:after{
    display: block;
    
}
.order-content .name .checkbox-wrapper input ~ .checkmark{}
.order-content .product-list .item{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    padding: 8px 0;
    border-top: 1px solid white
    
}
.order-content .product-list .item .info{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1
    
}
.order-content .product-list .item .info .item-price{
    color: #9B9B9B;
    
}
.order-content .product-list .item:first-child{
    padding: 0;
    padding-bottom: 8px;
    border: none;
    
}
.order-content .option{
    width: 120px;
    position: relative
    
}
.order-content .option .select{
    background-color: #7ED321;
    border-radius: 5px;
    padding: 0 16px;
    height: 34px;
    line-height: 34px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    cursor: pointer;
    -webkit-transition: .2s;
    transition: .2s
    
}
.order-content .option .select span{
    color: white;
    font-weight: 700;
    font-size: 16px;
    line-height: 34px;
    margin-right: 8px;
    
}
.order-content .option .select i {
    color: white;
    
}
.order-content .option .paid{
    background-color: #7ED321;
    width: 88px;
    -webkit-transition: .2s;
    transition: .2s;
    
}
.order-content .option .unpaid{
    background-color: #9B9B9B;
    width: 110px;
    -webkit-transition: .2s;
    transition: .2s;
    
}
.order-content .option .shipping{
    background-color: #F5A623;
    width: 125px;
    -webkit-transition: .2s;
    transition: .2s;
    
}
.order-content .option .done{
    background-color: #4A90E2;
    width: 95px;
    -webkit-transition: .2s;
    transition: .2s;
    text-decoration: none
    
}
.order-content .option .done span{
    text-decoration: none;
    
}
.order-content .option .list{
    position: absolute;
    top: 42px;
    left: 16px;
    background-color: white;
    z-index: 10;
    display: none;
    width: 100px;
    -webkit-box-shadow: 1px 2px 10px #9b9b9b;
            box-shadow: 1px 2px 10px #9b9b9b
    
}
.order-content .option .list input{
    padding: 8px 16px;
    width: 100%;
    cursor: pointer
    
}
.order-content .option .list input:hover{
    background-color: black;
    color: white;
    
}
.order-content .option .visible{
    display: block;
    
}
.order-content:nth-child(even){
    background-color: #EBEBEB;
}
div.content-done span, div.content-done p{
    color: #9B9B9B;
    text-decoration: line-through;
    
}
div.content-unpaid span, div.content-unpaid p{
    color: #9B9B9B;
    
}


