.header {
    font-family: 'Open Sans';
    position: relative;
    z-index: 100;
}

header .menu {
    width: 100%;
    height: 70px;
    background: black;
    padding: 21px 42px;
}

header .menu ul {
    list-style: none;
    position: relative;
    width: 940px;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

header .menu li span {
    color: white;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    color: #9B9B9B;
    padding: 0 7.5px;
    margin: 0 12.5px;
}

header .menu li .logo {
    font-size: 24px;
    display: inline-block;
    margin: 0;
    padding: 0;
    margin-right: 80px;
    position: relative;
}

header .menu li .staus {
    position: absolute;
    right: 0;
    top: 0;
    margin: 0;
}

header .menu .selected li {
    position: relative;
}

header .menu .selected li:after {
    content: '';
    background-color: white;
    height: 4px;
    width: 100%;
    position: absolute;
    bottom: -29px;
    left: 0;
}

header .menu .selected li span {
    color: white;
}


