.product-item{
	display: grid;
	grid-template-columns: 210px repeat(2, 80px) 229px 2fr;
	grid-column-gap: 32px;
  	width: 940px;
  	margin: 0 auto;
  	padding: 20px 16px;
  	-webkit-transition: .2s color;
  	transition: .2s color
}


.product-item .item-name{
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex
}


.product-item .item-name .img{
	width: 50px;
	height: 50px;
	margin: 0 8px;
	background-size: cover;
}


.product-item .item-name .check-wrapper{
	position: relative;
	display: block;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	height: 15px;
	width: 15px;
	margin-top: 17px;
	margin-right: 5px
}


.product-item .item-name .check-wrapper .checkmark{
	position: absolute;
	top: 0;
	left: 0;
	height: 15px;
	width: 15px;
	background-color: white
}


.product-item .item-name .check-wrapper .checkmark:after{
	content: '';
	position: absolute;
	display: none;
	left: 5px;
	top: 2px;
	width: 3px;
	height: 6px;
	border: solid white;
	border-width: 0 2px 2px 0;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}


.product-item .item-name .check-wrapper input:checked ~ .checkmark{
	/* checkbox checked color */
	background-color: #9B9B9B
}


.product-item .item-name .check-wrapper input:checked ~ .checkmark:after{
	display: block;
}


.product-item .item-name .check-wrapper input ~ .checkmark{}


.product-item .item-name .title{
	margin-top: 17px;
}


.product-item .price{
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	margin-top: 17px;
}


.product-item .item-detail{
	margin-top: 17px
}


.product-item .item-detail .option{
	display: grid;
	grid-template-columns: 45px 45px 75.5px;
	grid-column-gap: 32px;
	padding: 8px 0;
	border-top: 1px solid white;
}


.product-item .item-detail .option:first-child{
	padding-top: 0;
	border: none;
}


.product-item .item-detail span{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	font-size: 16px;
	line-height: 21px;
}


.product-item .item-detail .inventory span{
	-webkit-box-align: end;
	    -ms-flex-align: end;
	        align-items: flex-end;
}


.product-item .status .select{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	line-height: 34px;
	width: 139px;
	height: 34px;
	background-color: #7ED321;
	border-radius: 4px;
	position: relative;
	margin-top: 17px;
	padding: 0 16px;
	-webkit-transition: .2s;
	transition: .2s
}


.product-item .status .select span{
	color: white;
	margin-right: 8px;
	cursor: pointer;
	font-weight: 700;
}


.product-item .status .select i {
	color: white;
}


.product-item .status .unpublished{
	width: 165px;
	background-color: #9B9B9B;
	-webkit-transition: .2s;
	transition: .2s;
}


.product-item .status .published{
	width: 139px;
	background-color: #7ED321;
	-webkit-transition: .2s;
	transition: .2s;
}


.product-item .status .list{
	display: none;
	position: absolute;
	left: 0;
	top: 42px;
	background-color: white;
	-webkit-box-shadow: 1px 2px 10px #9B9B9B;
	        box-shadow: 1px 2px 10px #9B9B9B
}


.product-item .status .list input{
	display: block;
	width: 145px;
	height: 40px;
	line-height: 34px;
	text-align: center;
	font-size: 16px;
	color: black;
	cursor: pointer
}


.product-item .status .list input:hover, .product-item .status .list input:active{
	background-color: #000;
	color: white;
}


.product-item .status .visible{
	display: block;
}


.product-item:nth-child(even){
	background-color: #e9e9e9;
}


.item-unpublish span{
	color: #9B9B9B;
	-webkit-transition: .2s color;
	transition: .2s color;
}


