section.product-List{
	width: 940px;
	margin: 30px auto 100px auto
	
}

section.product-List .product-status{
	margin-bottom: 16px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width: 100%;
	padding-left: 16px
	
}

section.product-List .product-status .selector{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1
	
}

section.product-List .product-status .selector .check-wrapper{
	display: inline-block;
	position: relative;
	cursor: pointer;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	height: 15px;
	width: 15px;
	margin-right: 5px
	
}

section.product-List .product-status .selector .check-wrapper .checkmark{
	position: absolute;
	top: 0;
	left: 0;
	height: 15px;
	width: 15px;
	background-color: white;
	border: 1px solid black;
	border-radius: 2px
	
}

section.product-List .product-status .selector .check-wrapper .checkmark:after{
	content: '';
	position: absolute;
	display: none;
	left: 4px;
	top: 1px;
	width: 3px;
	height: 6px;
	border: solid white;
	border-width: 0 2px 2px 0;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	
}

section.product-List .product-status .selector .check-wrapper input:checked ~ .checkmark{
	background-color: black
	
}

section.product-List .product-status .selector .check-wrapper input:checked ~ .checkmark:after{
	display: block;
	
}

section.product-List .product-status .addbtn{
	background-color: black;
	border-radius: 4px;
	height: 34px;
	line-height: 34px;
	padding: 0 16px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	cursor: pointer
	
}

section.product-List .product-status .addbtn h5{
	color: white;
	font-size: 16px;
	display: inline-block;
	margin-right: 8px;
	
}

section.product-List .product-status .addbtn i{
	color: white;
	
}

section.product-List .product-title{
	background-color: white;
	height: 40px;
	display: grid;
	width: 1024px;
	grid-template-columns: 210px repeat(2, 80px) repeat(3, .5fr) 2fr;
	grid-column-gap: 32px;
	width: 940px;
	margin: 0 auto;
	padding: 0 16px
	
}

section.product-List .product-title h5{
	font-size: 16px;
	line-height: 19px;
	font-weight: 900;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	
}

section.product-List .product-title h5:nth-child(1){
	padding-left: 24px;
	-webkit-box-pack: start;
	    -ms-flex-pack: start;
	        justify-content: flex-start;
	
}

section.product-List .product-title h5:nth-child(2),
		section.product-List .product-title h5:nth-child(3){
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end;
	
}

section.new-product{
	width: 940px;
	position: fixed;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	display: none
}

section.new-product .title{
	background-color: black;
	height: 64px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	padding: 0 32px
	
}

section.new-product .title span{
	color: white;
	font-size: 24px;
	line-height: 29px;
	font-weight: 900;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	
}

section.new-product .title i{
	color: white;
	font-size: 24px;
	cursor: pointer;
	
}

section.new-product .content{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 32px;
	background-color: white
	
}

section.new-product .content .side-left{
	margin-right: 30px
	
}

section.new-product .content .side-left .img-updater{
	position: relative;
	overflow: hidden;
	display: inline-block
	
}

section.new-product .content .side-left .img-updater .updater-btn{
	background-color: #EBEBEB;
	width: 380px;
	height: 115px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center
	
}

section.new-product .content .side-left .img-updater .updater-btn i{
	font-size: 33px;
	color: #757575;
	margin-bottom: 8px;
	
}

section.new-product .content .side-left .img-updater .updater-btn span{
	font-size: 16px;
	line-height: 24px;
	color: #757575;
	font-weight: 700;
	
}

section.new-product .content .side-left .img-updater input[type=file]{
	font-size: 100px;
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	
}

section.new-product .content .side-left .img-wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 380px
	
}

section.new-product .content .side-left .img-wrapper .img{
	width: 121px;
	height: 121px;
	margin-top: 8px;
	margin-right: 8px;
	background-size: cover;
	
}

section.new-product .content .side-left .img-wrapper .img:nth-child(3n){
	margin-right: 0;
	
}

section.new-product .content .side-right{
	width: 466px
	
}

section.new-product .content .side-right h2{
	font-size: 16px;
	line-height: 19px;
	font-weight: 700;
	margin-bottom: 8px;
	
}

section.new-product .content .side-right .product-title{
	width: 466px;
	height: 38px;
	border-radius: 4px;
	border: 1px solid #CCC;
	padding: 8px 16px;
	font-size: 16px;
	line-height: 24px;
	color: black;
	margin-bottom: 8px;
	
}

section.new-product .content .side-right .product-content{
	width: 466px;
	height: 172px;
	border-radius: 4px;
	border: 1px solid #CCC;
	padding: 8px 16px;
	font-size: 16px;
	line-height: 24px;
	color: black;
	margin-bottom: 20px;
	
}

section.new-product .content .side-right .price-wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 20px
	
}

section.new-product .content .side-right .price-wrapper .price{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 38px;
	line-height: 38px;
	margin-right: 8px
	
}

section.new-product .content .side-right .price-wrapper .price .price-title{
	background-color: #e8e8e8;
	border: 1px solid #ccc;
	border-radius: 4px 0px 0 4px
	
}

section.new-product .content .side-right .price-wrapper .price .price-title span{
	padding: 0px 16px;
	color: #55595C;
	font-size: 16px;
	line-height: 24px;
	
}

section.new-product .content .side-right .price-wrapper .price input{
	border: 1px solid #ccc;
	border-radius: 0px 4px 4px 0px;
	padding: 0px 16px;
	text-align: right;
	width: 100px;
	
}

section.new-product .content .side-right .size-wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex
	
}

section.new-product .content .side-right .size-wrapper .size-detail{
	height: 38px;
	line-height: 38px;
	margin-bottom: 8px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex
	
}

section.new-product .content .side-right .size-wrapper .size-detail span{
	display: inline-block;
	background-color: #ebebeb;
	border: 1px solid #ccc;
	border-radius: 4px 0 0 4px;
	padding: 0 16px;
	font-size: 16px;
	text-align: center;
	
}

section.new-product .content .side-right .size-wrapper .size-detail input, section.new-product .content .side-right .size-wrapper .size-detail select{
	border: 1px solid #ccc;
	padding: 0 16px;
	width: 70px;
	margin-right: 8px;
	border-radius: 0 4px 4px 0;
	font-size: 16px;
	text-align: center;
	
}

section.new-product .content .side-right .add-size{
	background-color: black;
	height: 38px;
	display: block;
	width: 466px;
	border-radius: 4px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-ms-align-items: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	cursor: pointer;
	margin-bottom: 30px
	
}

section.new-product .content .side-right .add-size span, section.new-product .content .side-right .add-size i{
	color: white;
	font-size: 16px;
	line-height: 24px;
	
}

section.new-product .content .side-right .add-size span{
	font-weight: 700;
	margin-right: 8px;
	
}

section.new-product .content .side-right .btn-wrapper{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: end;
	    -ms-flex-pack: end;
	        justify-content: flex-end
	
}

section.new-product .content .side-right .btn-wrapper .save{
	color: #757575;
	font-weight: 700;
	font-size: 16px;
	line-height: 34px;
	margin-right: 30px;
	cursor: pointer;
	
}

section.new-product .content .side-right .btn-wrapper .publish{
	background-color: black;
	border-radius: 4px;
	height: 34px;
	line-height: 34px;
	width: 101px;
	cursor: pointer
	
}

section.new-product .content .side-right .btn-wrapper .publish span{
	padding: 0 16px;
	color: white;
	font-size: 16px;
	line-height: 24px;
	font-weight: 700;
	
}

section.visible{
	display: block;
}

& .page-num{
        display: -moz-flex;
        display: -ms-flex;
        display: -o-flex;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: end;
            -ms-flex-pack: end;
                justify-content: flex-end;
        margin-top: 30px
    }

& .page-num i, & .page-num span{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	width: 40px;
	height: 40px;
	color: black;
	font-size: 16px;
	-webkit-transition: .2s;
	transition: .2s;
	background-color: white;
	border: 1px solid #ECEEEF;
	border-left: none;
	cursor: pointer
	
}

& .page-num i:hover,& .page-num span:hover{
	background-color: #9B9B9B;
	color: white;
	-webkit-transition: .2s;
	transition: .2s;
	
}

& .page-num i{
	font-size: 12px;
	
}

& .page-num i:nth-child(1){
	color: #9B9B9B;
	border-left: 1px solid #ECEEEF;
	
}

& .page-num .selected{
	background-color: black;
	color: white;
	
}


